<template>
  <ChartLine :data="data" :options="chartOptions" />
</template>

<script lang="ts">
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
} from 'chart.js';
import { Line as ChartLine } from 'vue-chartjs';

ChartJS.register(
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend
);

export default {
  components: {
    ChartLine,
  },

  props: {
    options: {
      type: Object,
      required: true,
    },

    data: {
      type: Object,
      required: true,
    },
  },

  created() {
    this.chartOptions = {
      responsive: true,
      maintainAspectRatio: false,
      border: {
        display: false,
      },
      scales: {
        y: {
          border: {
            display: false,
            dashOffset: 2,
          },
          ...this.options.y,
        },
        x: {
          border: {
            display: false,
          },
          ...this.options.x,
        },
      },
      plugins: {
        legend: {
          display: false,
        },
      },
    };
  },
};
</script>
