<template>
  <UiButton
    class="button-link"
    color="transparent"
    tag="a"
    decoration="underline"
    :href="href"
    @click="emitClick"
  >
    <UiTextString
      class="button-link__link"
      :size="size"
      weight="500"
      color="grey"
    >
      <slot />
    </UiTextString>
  </UiButton>
</template>

<script>
import UiButton from '@/components/core/ui-button/ui-button.vue';
import UiTextString from '@/components/core/ui-text-string/ui-text-string.vue';

export default {
  components: {
    UiButton,
    UiTextString,
  },

  props: {
    href: {
      type: String,
      required: false,
      default: undefined,
    },

    size: {
      type: String,
      required: false,
      default: 's',
    },
  },

  methods: {
    emitClick() {
      this.$emit('click');
    },
  },
};
</script>

<style>
.button-link {
  position: relative;
}

.button-link__link::before {
  content: '';
  position: absolute;
  width: 100%;
  height: 1px;
  bottom: 0px;
  background: rgba(62, 73, 88, 0.65);
}
</style>
