<template>
  <div class="select" v-click-outside="hideOptions">
    <div class="select__input" @click="showOptions">
      <div class="select__title">{{ value }}</div>
      <svg
        class="select__icon"
        width="9"
        height="10"
        viewBox="0 0 9 10"
        fill="none"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M4.19192 7.83838C4.22633 7.88834 4.27236 7.9292 4.32606 7.95742C4.37976 7.98564 4.43951 8.00039 4.50017 8.00039C4.56084 8.00039 4.62059 7.98564 4.67429 7.95742C4.72799 7.9292 4.77402 7.88834 4.80842 7.83838L8.18342 2.96338C8.22249 2.90715 8.2454 2.84129 8.24966 2.77295C8.25392 2.70462 8.23938 2.63642 8.20761 2.57577C8.17583 2.51512 8.12805 2.46434 8.06944 2.42895C8.01083 2.39355 7.94364 2.37489 7.87517 2.375H1.12517C1.05687 2.37528 0.989926 2.39418 0.931555 2.42966C0.873183 2.46514 0.825588 2.51586 0.793888 2.57637C0.762188 2.63688 0.747582 2.70489 0.751641 2.77307C0.7557 2.84126 0.77827 2.90706 0.816925 2.96338L4.19192 7.83838Z"
          fill="#979699"
        />
      </svg>
    </div>
    <div class="select__container" v-if="isOptionsVisible">
      <div
        class="select__option"
        :class="{ select__option_dark: option.title === value }"
        v-for="option in options"
        :key="option.value"
        @click="handleOptionClick(option.title)"
      >
        {{ option.title }}
      </div>
    </div>
  </div>
</template>

<script>
import ClickOutside from 'vue-click-outside';

export default {
  directives: {
    ClickOutside,
  },

  props: {
    value: {
      type: String,
      required: true,
    },

    options: {
      type: Array,
      required: true,
    },
  },

  data() {
    return {
      isOptionsVisible: false,
    };
  },

  methods: {
    showOptions() {
      this.isOptionsVisible = true;
    },

    hideOptions() {
      this.isOptionsVisible = false;
    },

    handleOptionClick(title) {
      this.$emit('input', title);
      this.hideOptions();
    },
  },
};
</script>

<style>
.select {
  display: inline-block;
  position: relative;
  font-size: 14px;
  line-height: 16px;
  font-weight: 500;
  color: rgba(62, 73, 88, 0.65);
  width: 100%;
  border: 1px solid rgba(151, 150, 153, 0.24);
  cursor: pointer;
}

.select__input {
  border-radius: 2px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 8px;
}

.select__icon {
  height: 9px;
}

.select__icon {
  margin-left: 8px;
}

.select__container {
  position: absolute;
  width: 100%;
  top: 40px;
  border: 1px solid rgba(151, 150, 153, 0.24);
  border-radius: 2px;
  z-index: 999;
}

.select__option {
  text-align: left;
  padding: 8px;
  background-color: #fff;
}

.select__option_dark {
  color: #007dbc;
}
</style>
